import { WatchlistDataTablePercentageCell } from "../../WatchlistDataTableCells/WatchlistDataTablePercentageCell"
import { genericColumn } from "../../genericColumn"

function operatingReturnOnAssetsTtm(tableName: string) {
  const title = "Operating ROA (LTM)"
  return genericColumn({
    endpoint: "companyKeyMetrics",
    columnKeys: ["operatingReturnOnAssetsTtm"],
    size: 110,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "Operating profit from all assets.",
    cell: info => {
      return (
        <WatchlistDataTablePercentageCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyKeyMetrics}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} recently reported about operating return on assets or other relevant financial measures?`,
              value:
                info.row.original.financialData.companyKeyMetrics?.data?.operatingReturnOnAssetsTtm,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={
            (info.row.original.financialData.companyKeyMetrics?.data?.operatingReturnOnAssetsTtm ??
              0) * 100
          }
        />
      )
    },
  })
}

export { operatingReturnOnAssetsTtm }
